#gooey-button {
  padding: 1rem;
  font-size: 3rem;
  border: none;
  color: #0c1016;
  -webkit-filter: url("#gooey");
          filter: url("#gooey");
  position: relative;
  background-color: #41f271;
  margin-top: 32px;
  padding-right: 30px;
  padding-left: 30px;
}
#gooey-button:focus {
  outline: none;
}
#gooey-button .bubbles {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
#gooey-button .bubbles .bubble {
  background-color: #00ff80;
  border-radius: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  z-index: -1;
}
#gooey-button .bubbles .bubble:nth-child(1) {
  left: 64px;
  width: 25px;
  height: 25px;
  -webkit-animation: move-1 3.02s infinite;
  animation: move-1 3.02s infinite;
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s;
}
